  searchNewMessages(messageRecords) {    
    // console.log("$$$$$$$$$$$$$$$ Initial Conversations $$$$$$$$$$$$$$$4")
    // console.log(this.state.conversations)
    // console.log("\t Searching for new messages");
    for (let i = 0; i < messageRecords.length; i++) {
      let record = messageRecords[i];
      if (
        record.Message.from != "bot@hermes" &&
        Date.parse(record.creation_date) > Date.parse(this.state.lastSeen)
      ) {
        this.setState({ latest_conversation: record.conversation_id });

       

        //Add notification if non active conversations
        if (
         ( record.conversation_id !=
            this.props.conversationData.conversation_id || this.state.isOpened == false) &&
          record.Message.from != this.props.logInStatus.username
        ) {

         
          this.setState(
            {
              lastSeen: record.creation_date,
              newMessage: "New message from " + record.Message.from,
            },
            () => {
              // console.log("\tnew message from" + record.Message.from)
            }
          );
        }

        //Check if new message if from existing conversations
        let conversation_index = this.state.conversations.findIndex(
          (p) => p.id == record.group_id
        );

        //Preparing conversation
        let conversation = {
          id: record.group_id,
          name:
            record.group_type == "individual" || record.group_type == "BOT"
              ? record.fname + " " + record.lname
              : record.fname + " " + record.lname + " and others",
          fName:
            record.group_type == "individual" || record.group_type == "BOT"
              ? record.fname
              :record.group_name,
          lName: record.lname,
          group_type: record.group_type,
          username: record.username,
          isNew:
            record.Message.from == this.props.logInStatus.username
              ? false
              : true,
          updatedtile: new Date().toUTCString(),
        };

        //If conversation not exitst
        if (conversation_index == -1) {
          this.setState(
            {
              conversations: [...this.state.conversations, conversation],
            },
            () => console.log("New conversation Added")
          );
        } else {
          let current_conversations = this.state.conversations;
          let { conversations } = this.state;
          const active_conversation = conversations[conversation_index];
          active_conversation.isNew =
            record.conversation_id ==
              this.props.conversationData.conversation_id ||
            record.Message.from == this.props.logInStatus.username
              ? false
              : true;

          conversations.splice(conversation_index, 1);
          conversations = [...conversations, active_conversation];
          this.setState({ conversations });
      
        }
        this.updateLastSeen(new Date());
      }
      // else {
      //   this.setState({ newMessage: "" });
      // }
    }
  }

