import React, { Component } from "react";
import Container from "@material-ui/core/Container";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import Button from "@material-ui/core/Button";
import { connect } from "react-redux";
import axios from "axios/index";
import uuid from "react-uuid";
import './login.css'
import { makeStyles } from '@material-ui/core/styles';
import Alert from '@material-ui/lab/Alert';
import CheckIcon from '@material-ui/icons/Check';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import {chatbot_api_host,chatbot_api_port,nlp_gateway_host,nlp_gateway_port} from '../../config';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
  },
}));

export class LoginForm extends Component {

    constructor(props) {
        super(props)
    
        this.state = {

          isLogIn:0,
             credentials:{
                 username:"",
                 password:"",
             }
        }

        this.setCredential =this.setCredential.bind(this);
        this.onLoginClick = this.onLoginClick.bind(this);
        this.onEnterPress = this.onEnterPress.bind(this);
    }
    

    setCredential(e,item){
   
        let  value = e.target.value;
        if(item == "username"){
            this.setState(prevState => ({
                credentials: {                   // object that we want to update
                    ...prevState.credentials,    // keep all other key-value pairs
                    username: value   // update the value of specific key
                }
            })) 
        }

        else
        this.setState(prevState => ({
            credentials: {                   // object that we want to update
                ...prevState.credentials,    // keep all other key-value pairs
                password: value    // update the value of specific key
            }
        })) 
    }


    onEnterPress(e){
      
      if (e.key === "Enter" && this.state.username !="" && this.state.password != "" ) {
        this.onLoginClick();
     }
    }

    async onLoginClick(){
        
        let credentials = this.state.credentials; 
        const request_url =
        "http://"+chatbot_api_host+":"+chatbot_api_port+"/varifyUser?" +
        "username=" +
        credentials.username +
        "&password=" +
        credentials.password;

        await axios
        .get(request_url)
        .then(
          function(response) {             
            if(response.data.length>0){
              this.setState({isLogIn:1})
              // Dispaly Agent window directly if it is an agent
              // if(response.data[0].user_type == "agent"){
              //   this.props.set_talk_to_what("agent");     
                           
              //   this.props._onPlusButtonClick();
              // }          
              //Set user details to  Redux store once he successfully LoggedIn         
              
              
              //************** SET user data in store loginStatus=true **********//
               this.props.set_User_Data_After_Login(
                 {firstName:response.data[0].fname,
                  lastName:response.data[0].lname,
                  userType:response.data[0].user_type,
                  username:response.data[0].username})
              }

              else{
                this.refs.password_input_box.value="";
                this.setState({isLogIn:2})
              }
            
          }.bind(this)
        )
        .catch(function(err){
          //console.log(err)

        });

     
    }

  render() {
    return (
      <React.Fragment>
        <Container maxWidth="sm">
          <FormControl>
            <InputLabel htmlFor="username">Email address</InputLabel>
            <Input 
            value={this.state.credentials.username} 
            id="username" 
            aria-describedby="my-helper-text"            
            onChange={(e)=>this.setCredential(e,'username')} 
            onKeyPress={(e)=>this.onEnterPress(e)}
            type="email"/>
            <FormHelperText id="username-helper-text">
              Enter Your username
            </FormHelperText>
          </FormControl>
          <br></br>
          <FormControl>
            <InputLabel htmlFor="password">Password</InputLabel>
            <Input
              type="password"
              id="password"
              aria-describedby="my-helper-text"
              value={this.state.credentials.password}
              ref="password_input_box"
              onChange={(e)=>this.setCredential(e,'password')}
              onKeyPress={(e)=>this.onEnterPress(e)}
             
            />
            <FormHelperText id="password-helper-text">
              enter your password
            </FormHelperText>
          </FormControl>
          <br />
          <FormControl>
            <Button variant="contained" color="primary" onClick={this.onLoginClick}>
              Log In
            </Button>
          </FormControl>

      <FormControl>
      <br/><br/>
{  this.state.isLogIn==2 && 
         <Alert variant="outlined" severity="error">
        Invalid credentials!
       </Alert>}

          </FormControl>
        </Container>
      </React.Fragment>
    );
  }
}

const mapStateToProps = state =>{
 return{
   logInData:state.logInStatus 
 }
}

const mapDispatchToProps = dispatch =>{
return{
  set_User_Data_After_Login: logInData =>{
        dispatch({type:"LOGIN_SUCCESS_ACTION",payload:logInData});
    },
    set_talk_to_what: talk_to => {
      dispatch({ type: "TALK_TO_WHAT_ACTION", payload: talk_to });
    }, 
    set_conversation_id: id => {
      dispatch({ type: "CONVERSATION_ID_ACTION", payload: id });
    },
    set_group_id: id => {
      dispatch({ type: "GROUP_ID_ACTION", payload: id });
    },
    set_to_participantID: id => {
      dispatch({ type: "TO_PARTICIPANT_ID_ACTION", payload: id });
    },
    set_from_participantID: id => {
      dispatch({ type: "FROM_PARTICIPANT_ID_ACTION", payload: id });
    } 
   
  
}
}

export default connect(mapStateToProps,mapDispatchToProps)(LoginForm);
