import React, { Component } from "react";
import axios from "axios/index";
import "./chatbot.css";
import Cookies from "universal-cookie";
import Message from "./Message";
import Card from "./Card";
import MyCarousel from "./Carousal";
import { connect } from "react-redux";
import {
  update_botStatus_inactive,
  update_botStatus_active,
} from "../../store/actions/botStatusAction";
import {
  update_botTyping_to_true,
  update_botTyping_to_false,
} from "../../store/actions/botTypingAction";
import { bot_message_added } from "../../store/actions/botMessagesLengthAction";
import store from "../../store/index";
import MinimizedChatbot from "./MinimizedChatbot";
import Draggable from "react-draggable"; // The default
import { DraggableCore } from "react-draggable"; // <DraggableCore>
import StaticCarousel from "../../components/chatbot/carousel/Carousel";
import uuid from "react-uuid";
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
import MinIframe from "./MinIframe";
import MaterialTableDemo from "./TableSorted";
import ModalOne from "../../components/modals/ModalOne";
import { open_modal } from "../../store/actions/modalStatusAction";
import Drawer from "./Drawer";
import chat_icon from "../../images/Bot-Academy-logo-head-1920.png";
import AgentList from "./AgentList";
import ChatBotHeader from "./ChatBotHeader";
import LoginForm from "./LoginForm";
import ListEmployees from "./ListEmployees";
import AttachementMenu from "./AttachementMenu";
import {
  chatbot_api_host,
  chatbot_api_port,
  nlp_gateway_host,
  nlp_gateway_port,
} from "../../config";

class Chatbot_Talk_To_Bot extends Component {
  messagesEnd = "";
  constructor(props) {
    super(props);
    this._handleInputKeyPress = this._handleInputKeyPress.bind(this);
    this.refeshChatbot = this.refeshChatbot.bind(this);
    this.scrollToBottom = this.scrollToBottom.bind(this);
    this.getReddisResponse = this.getReddisResponse.bind(this);
    this._handleSendButton = this._handleSendButton.bind(this);
    this._onPlusButtonClick = this._onPlusButtonClick.bind(this);
    // this._handleKeyDown =this._handleKeyDown.bind(this);
    this._handleKeyUp = this._handleKeyUp.bind(this);
    this.renderListMessages = this.renderListMessages.bind(this);
    this.toggleDesignation = this.toggleDesignation.bind(this);
    // this.talkToSomeOne = this.talkToSomeOne.bind(this);
    this._onEmployeeListBackButton = this._onEmployeeListBackButton.bind(this);
    this._toggleAttachButton = this._toggleAttachButton.bind(this);
    this.df_text_query = this.df_text_query.bind(this)

    // this.hide = this.hide.bind(this);
    // this.show = this.show.bind(this);

    this.state = {
      messages: [],
      showBot: true,
      openMenu: false,
      toggleMenuClass: "Hide-Bottom-Menu",
      is_mounted: false,
      inputHistory: [],
      inputPointer: 0,
      designationSelected: false,
      employeeList: [],
      isattachmentMenuOpen: false,
    };
  }

  componentWillUnmount() {
    this.setState({ is_mounted: false });
  }

  shouldComponentUpdate(p, s) {
    return true;
  }
  toggleDesignation(status, employeeList) {
    this.setState({ employeeList: employeeList });
    this.setState({ designationSelected: status });
  }
  _onEmployeeListBackButton(status) {
    this.setState({ designationSelected: status });
  }

  async df_text_query(text) {
  if(text == 'connect to agent' || text == 'connect me' || text == '@connect'){
this.props.set_talk_to_what("agent")
  }

  else{
    let message_uuid = uuid();
    this.props.update_botTyping_to_true();

    if (text == "") {
      let says = {
        speaks: "bot",
        msg: {
          text: {
            text: "Ohoo!1 Plese type someting",
          },
        },
      };
      this.setState({ messages: [...this.state.messages, says] });
      this.props.update_botTyping_to_false();
    } else {
      let says = {
        speaks: "me",
        msg: {
          text: {
            text: text,
          },
        },
      };

      this.setState({ messages: [...this.state.messages, says] });

      this.props.bot_message_added();
      this.props.update_botTyping_to_true();

      let request = {
        MESSAGE: {
          QUERY: text,
          QUERY_UUID: message_uuid,
          SESSION_ID:this.props.sesstionDetails.session_id,
          USER_NAME:this.props.logInData.firstName
        },
      };

      console.log("\n NLP Gateway payload\n")
        console.log(request)
      await axios
        .post("https://"+nlp_gateway_host+":"+nlp_gateway_port+"/conversion/request/v0", request, {
        headers: { "Content-Type": "application/x-www-form-urlencoded" }
        })

     //   .get(
       //   "http://" + nlp_gateway_host + ":" + nlp_gateway_port + "/response"
        //)
        .then(
          function (response) {
            this.sleep(2000);
            this.getReddisResponse(response, message_uuid);
          }.bind(this)
        )
        .catch(function (error) {
          //console.log(error);
        });

      this.props.update_botTyping_to_false();
    }
  }
  }




  sleep(miliseconds) {
    var currentTime = new Date().getTime();
    while (currentTime + miliseconds >= new Date().getTime()) {}
  }

  async getReddisResponse(response, message_uuid) {
    if (response.data.MESSAGE.ERRORCODE == "00") {
      var reqBody = {
        MESSAGE: {
          QUERY: "hi",
          QUERY_UUID: message_uuid,
          SESSION_ID:this.props.sesstionDetails.session_id,
          USER_NAME:this.props.logInData.firstName
        },
      };

      reqBody = JSON.stringify(reqBody);

      await axios
         .post("https://"+nlp_gateway_host+":"+nlp_gateway_port+"/conversion/response/v0", reqBody, {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded"
            }
          })
       // .get(
        //  "http://" + nlp_gateway_host + ":" + nlp_gateway_port + "/response2"
       // )
        .then(
          function (response) {
            let Defaultsayss = {
              speaks: "bot",
              msg: {
                text: {
                  text: "Sorry!! Response is empty!!",
                },
              },
            };

            let sayss = {
              speaks: "bot",
              msg: {
                text: {
                  text: "hi How can I help you",
                },
              },
            };
            //  for(let i=0; i<response.data.length;i++){
            if (
              response.data.MESSAGE &&
              response.data.MESSAGE["BODYTYPE"] == "STRING"
            ) {
              //Adding a message if exist
              if (
                response.data.MESSAGE.BODY &&
                response.data.MESSAGE.BODY != ""
              ) {
                let ResponseText = response.data.MESSAGE.BODY;
                sayss = {
                  speaks: "bot",
                  msg: {
                    text: {
                      text: ResponseText,
                    },
                    url: "",
                    tableItems: [],
                  },
                };
                this.setState({
                  messages: [
                    ...this.state.messages,
                    ResponseText == "" ? Defaultsayss : sayss,
                  ],
                });
              }

              //Adding an IFrame if exist
              if (
                response.data.MESSAGE.IFRAME &&
                response.data.MESSAGE.IFRAME != ""
              ) {
                let iframeSays = {
                  speaks: "bot",
                  url: response.data.MESSAGE.IFRAME,
                  type: "iframe",
                };
                this.setState({
                  messages: [...this.state.messages, iframeSays]});
                this.props.open_modal("iframe",response.data.MESSAGE.IFRAME)
                  
              }
            }
            else if (
              response.data.MESSAGE &&
              response.data.MESSAGE["BODYTYPE"] == "LIST"
            ) {
              if (response.data.MESSAGE.BODY.length == 0 ) {
                //if no record in tableData  let Defaultsayss = {
                const emplyTable = {
                  speaks: "bot",
                  msg: {
                    text: {
                      text: "No records abailable",
                    },
                  },
                };

                if(response.data.MESSAGE.IFRAME == "" ){
                  this.setState({
                    messages: [...this.state.messages, emplyTable],
                  });
                }
              }                        
              else{
                this.renderListMessages(response);
              }


              if (response.data.MESSAGE.IFRAME && response.data.MESSAGE.IFRAME != "") {
                let iframeSays = {
                  speaks: "bot",
                  url: response.data.MESSAGE.IFRAME,
                  type: "iframe",
                };
          
                this.setState(
                  {
                    messages: [...this.state.messages, iframeSays],
                  },
                  
                );
                this.props.open_modal("iframe",response.data.MESSAGE.IFRAME)
              }


            } 
            
            else {
              this.setState({
                messages: [...this.state.messages, Defaultsayss],
              });
            }
            // }
          }.bind(this)
        )
        .catch(function (error) {
          console.log(
            "***************Response API Error: ***********88" + error
          );
        });
    }
  }

  renderListMessages(response) {
    let message=  response.data.MESSAGE;
    let tableItems = response.data.MESSAGE.BODY[0];
    let data = response.data.MESSAGE.BODY;
    let columns = [];
    let keys = Object.keys(tableItems);

    for (let i = 0; i < keys.length; i++) {
      columns.push({
        title: keys[i],
        field: keys[i],
      });
    }

    // for (let count = 0; count < response.data.MESSAGE.BODY.length; count++) {
    for (let i = 0; i < response.data.MESSAGE.BODY.length; i++) {
      for (let j = 0; j < columns.length; j++) {
        let propName = columns[j].field;
        // data.push({
        //   propName : tableItems[i][columns[j].title]
        // })
        data[i][propName] = response.data.MESSAGE.BODY[i][columns[j].title];
      }
    }



    // }

    const tableData = {
      speak: "bot",
      type: "tableList",
      text: response.data.MESSAGE.HEADER,
      columns,
      data,
    };

    this.setState({
      messages: [...this.state.messages, tableData],
    });
    this.props.set_table_list_data({ columns, data });
    this.props.open_modal("dataList","")
  }

  refeshChatbot() {
    let says = {
      speaks: "bot",
      msg: {
        text: {
          text: "hi! Welcome again!! How can I help you",
        },
      },
    };
    this.setState({ messages: [says] });
    this.props.update_botTyping_to_false();
    // window.location.reload(false);
  }

  componentDidMount() { 
    this.setState({ is_mounted: true });

    // this.messagesEnd = `<div style={{ float:"left", clear: "both" }}
    // ref={(el) => { this.messagesEnd = el; }}>
    // </div>`;
    let says = {
      speaks: "bot",
      msg: {
        text: {
          text: `Hello ${this.props.logInData.firstName}! This is Hermes, the world's first Big-Data Bot with Sentient NLP .
          I also come with a dynamic UI. You can drag and drop me at any corner of the screen.       
          You can also connect to your managers or team members by clicking the plus icon at the bottom of the screen.`
        },
      },
    };

    this.setState({ messages: [...this.state.messages, says] });
  }

  scrollToBottom = () => {
    // this.messagesEnd.current.scrollIntoView({ behavior: 'smooth' })
    // this.refs.chat_box_ref.scrollTo(0);

    if (this.refs.chat_box_ref) {
      var cc = this.refs.chat_box_ref;
      cc.scrollTop = cc.scrollHeight - cc.clientHeight;
    }
  };

  componentDidUpdate(pp, ps) {
    if (this.refs.botInput) {
      this.refs.botInput.focus();
    }

    this.scrollToBottom();
  }

  renderCards(cards) {
    return cards.map((card, i) => <Card key={i} payload={card.structValue} />);
  }

  renderOneMessage(message, i) {
    if (message.msg && message.msg.text && message.msg.text) {
      return (
        <React.Fragment key={i}>
          {/* {this.props.isTyping == true && <div>  <img className="bot-typing" src="image/typing.gif"></img> </div>  }
           */}
          <Message
            key={i}
            speaks={message.speaks}
            text={message.msg.text.text}
            isTyping={this.props.isTyping}
            url={message.msg.url ? message.msg.url : ""}
            listData={message.msg.listData}
            from="bot@hermes"
          />
          {/* {
            message.speaks == 'bot' &&   < MinIframe></MinIframe>
          } */}
        </React.Fragment>
      );
    } else if (message.type == "tableList" && message.columns && message.data) {
      //  return (<ModalOne type="list" tableData={message.msg.tableItems} />)
      return (
        <Message
          speaks="bot"
          isTyping={this.props.isTyping}
          columns={message.columns}
          data={message.data}
          text={message.text}
          from="bot@hermes"
        />
      );
    } else if (
      message.msg &&
      message.msg.payload &&
      message.msg.payload.fields &&
      message.msg.payload.fields.cards
    ) {
      const items = [];
      for (const SuggestedItem of message.msg.payload.fields.cards.listValue
        .values) {
        items.push(<Card key={i} payload={SuggestedItem.structValue} />);
      }
      return <div key={i}>{items}</div>;
    } else if (message.type == "iframe") {
      return <MinIframe url={message.url}></MinIframe>;
    }

    this.scrollToBottom();
  }

  toggleSize() {}

  renderMessage(stateMessage) {
    if (stateMessage) {
      return stateMessage.map((message, i) => {
        return this.renderOneMessage(message, i);
      });
    } else {
      return null;
    }
  }

  _handleInputKeyPress(e) {
    if (e.key === "Enter" && e.target.value != "") {
      this.df_text_query(e.target.value);
      this.setState({
        inputHistory: [...this.state.inputHistory, e.target.value],
      });
      this.setState({ inputPointer: this.state.inputHistory.length });
      e.target.value = "";
    }
  }
 
  _handleKeyUp(e) {
    if (e.key == "ArrowUp") {
      if (
        this.state.inputHistory.length > 0 &&
        this.state.inputPointer < this.state.inputHistory.length - 1
      ) {
        e.target.value = this.state.inputHistory[this.state.inputPointer + 1];
        this.setState({ inputPointer: this.state.inputPointer + 1 });
      }
    } else if (e.key == "ArrowDown") {
      if (this.state.inputHistory.length > 0 && this.state.inputPointer >= 0) {
        e.target.value = this.state.inputHistory[this.state.inputPointer];
        this.setState({ inputPointer: this.state.inputPointer - 1 });
      }
    }
  }

  _onPlusButtonClick() {
    if (this.state.isattachmentMenuOpen == true) {
      this.setState({ isattachmentMenuOpen: false });
    }
    if (this.state.openMenu == false) {
      this.setState({ openMenu: true });
    } else {
      this.setState({ openMenu: false });
      this.setState({ designationSelected: false });
    }


   

  }

  _handleSendButton() {
    let userMessage = this.refs.botInput.value;
    if (userMessage != "") {
      this.df_text_query(userMessage);
      this.refs.botInput.value = "";
    }
  }

  _toggleAttachButton() {
    if (this.state.openMenu == true) {
      this.setState({ openMenu: false });
    }
    this.setState({ isattachmentMenuOpen: !this.state.isattachmentMenuOpen });
    //console.log("Attachment menuOpen? " + this.state.isattachmentMenuOpen);
  }

  render() {
    // if (this.state.showBot){
    return (
      <React.Fragment>
        {/* <div className="chatbot-Wrapper effect8 z-depth-5"> */}
        <ChatBotHeader
          update_botStatus_inactive={this.props.update_botStatus_inactive}
          refeshChatbot={this.refeshChatbot}
        />

        <div id="chatbot" ref="chat_box_ref" disabled={this.state.openMenu}>
          {this.renderMessage(this.state.messages)}
          <div ref="last_chat_message" />
        </div>

        {/* <div
          id="menuWindow"
          className={
            this.state.openMenu ? "Bottom-menu active" : " Bottom-menu"
          }
        >
          {this.props.isLogIn ? (
            this.props.set_talk_to_what("agent")
          ) : (
            <LoginForm _onPlusButtonClick={this._onPlusButtonClick}></LoginForm>
          )}
        </div> */}

        {/* <div className="carousel-wrapper">
                    <StaticCarousel />
                  </div> */}

        {/* <AttachementMenu
          classNames={
            this.state.isattachmentMenuOpen
              ? "Attachementmenu-wrapper active"
              : "Attachementmenu-wrapper"
          }
        /> */}

        <div className="input-wrapper top-only-shadow">
          <div
            className="add-button-wrapper blinkingX"
            onClick={()=>this.props.set_talk_to_what("agent")}
          >
            <span disabled={this.props.isTyping}>
              <i class="material-icons dp48 ">add_circle_outline</i>
            </span>
          </div>

          {this.props.isTyping ? (
            <p className="chatbotInputBox">Bot is Typing...</p>
          ) : (
            <div className="chatbotInputBox">
              <input
                className="input-box"
                type="text"
                onKeyPress={this._handleInputKeyPress}
                onKeyDown={this._handleKeyDown}
                onKeyUp={this._handleKeyUp}
                disabled={this.props.isTyping || this.state.openMenu}
                placeholder="Please write your query"
                ref="botInput"
              ></input>
            </div>
          )}
{/* 
          {this.props.isTyping == false && (
            <div className="attach-button" onClick={this._toggleAttachButton}>
              <span>
                <i class="material-icons dp48">attachment</i>
              </span>
            </div>
          )} */}

          {this.props.isTyping == false && (
            <div
              className="send-button-wrapper"
              onClick={this._handleSendButton}
            >
              <span disabled={this.props.isTyping || this.state.openMenu}>
                <i className="material-icons dp48">send</i>
              </span>
            </div>
          )}
        </div>
      </React.Fragment>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    update_botStatus_inactive: () => {
      dispatch(update_botStatus_inactive);
    },
    update_botStatus_active: () => {
      dispatch(update_botStatus_active);
    },
    update_botTyping_to_true: () => {
      dispatch(update_botTyping_to_true);
      //    setTimeOut(() =>   dispatch(update_botTyping_to_true) , 3000)
    },
    update_botTyping_to_false: () => {
      dispatch(update_botTyping_to_false);
      // this.props.setTimeOut(() =>   dispatch(update_botTyping_to_false) , 3000)
    },

    bot_message_added: () => {
      dispatch(bot_message_added);
    },
    set_talk_to_what: (talk_to) => {
      dispatch({ type: "TALK_TO_WHAT_ACTION", payload: talk_to });
    },
    set_table_list_data: (tableData) => {
      dispatch({ type: "TABLE_DATA_ACTION", payload: tableData });
    },

    open_modal: (type,url) => {
      dispatch({ type: "MODAL_TYPE_ACTION", payload: type });
      dispatch({ type: "MODAL_URL_ACTION", payload: url });
      dispatch(open_modal);
    },
    set_conversation_id: (id) => {
      dispatch({ type: "CONVERSATION_ID_ACTION", payload: id });
    },
    set_group_id: (id) => {
      dispatch({ type: "GROUP_ID_ACTION", payload: id });
    },
    set_to_participantID: (id) => {
      dispatch({ type: "TO_PARTICIPANT_ID_ACTION", payload: id });
    },
    set_from_participantID: (id) => {
      dispatch({ type: "FROM_PARTICIPANT_ID_ACTION", payload: id });
    }
  };
};

const mapStateToProps = (state) => {
  return {
    isOpened: state.botStatus.isOpened,
    botMessagesLength: state.botMessagesLength.length,
    isTyping: state.isTyping.isTyping,
    talk_to_what: state.talk_to_what.name,
    isLogIn: state.logInStatus.isLogIn,
    userType: state.logInStatus.userType,
    logInData: state.logInStatus,
    sesstionDetails:state.sessionDetails
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Chatbot_Talk_To_Bot);
